



import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap'

function Modal5
() {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    return (
        <>

            <h1 className="mb-5 text-decoration-underline serviceHover"
                onClick={handleShow}
                style={{  position: "absolute", cursor: "pointer" }}>Education
                </h1>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Empowerment Through Education
                    </Modal.Title>
                </Modal.Header>
                <img src="data:image/jpeg;base64," alt=""/>
                <Modal.Body>With the help of loyal donors, sponsors and fundraisers, our grass roots organisation has provided 178 disadvantaged Pakistan children, from grades 1 to 12, with a personal education programme tailored specifically to their needs. Our personal approach to charitable giving enables us to provide regular feedback to the individuals, businesses and schools who have decide to donate to help our Pakistan children; if you, your colleagues or students donate to the SOS Africa Children's Charity, we can show the invaluable impact made by your charitable donation. If you decide to donate money to our Pakistan charity today, 100% of your contribution will provide:</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleClose}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default Modal5


